// MODULE DES GRILLES RESPONSIVE





// GRID SYSTEM 12 colonnes
.grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $gapV $gapH;
    &.grid-no-gap {
       gap: 0 0;
    }
    > * {
        grid-column: span 12;
        grid-row: span 1;
    }
}

// GRID SYSTEM pour ECRANS MOBILES
    .container {
        width: 100%;
        margin: 0 auto;
        padding: 0 1em;
    }
    
    @for $i from 1 through 12 {
        .col-xs-#{$i} {
            grid-column: span + ' ' + $i;
        }
        .row-xs-#{$i} {
            grid-row: span + ' ' + $i;
        }
    }


// GRID SYSTEM pour ECRANS MEDIUM
@media (min-width: 768px) {
    .container {
        width: 720px;
        margin: 0 auto;
        padding: 0;
    }

    @for $i from 1 through 12 {
        .col-m-#{$i} {
            grid-column: span + ' ' + $i;
        }
        .row-m-#{$i} {
            grid-row: span + ' ' + $i;
        }
    }
}

 

